import React from 'react';

import {Page, Eventcalendar, getJson, Toast, Popup} from '@mobiscroll/react';
import "./student.css"

function Teacher({myCurrentEvents, uploadedPictureUrl, setCurrentDate}) {

    const [myEvents, setEvents] = React.useState([]);
    const [isPopupOpen, setPopupOpen] = React.useState(false);
    const [openEvent, setOpenEvent] = React.useState({})

    React.useEffect(()=>{
        setEvents(myCurrentEvents)
    },[myCurrentEvents])




    const onEventClick = React.useCallback((event) => {
        setOpenEvent(event.event)
        console.log(event.event)
        setPopupOpen(true);
    }, []);

    const onSelectedPage = React.useCallback((event) => {
        setCurrentDate(event.firstDay);
    }, []);

    const view = React.useMemo(() => {
        return {
            schedule: {

                type: 'day',
                size: 1, // Set the scheduler size to 1 to make it smaller
                startTime: '06:00',
                endTime: '23:00',
            }
        };
    }, []);

    return <div>
        <div className="picture-window">
            <img src={uploadedPictureUrl} alt="Uploaded"/>
        </div>
        <div className="calendar">
            <Eventcalendar
                height={"600px"}
                theme="ios"
                themeVariant="light"
                dragToMove={true}
                dragToResize={true}
                eventDelete={true}
                data={myEvents}
                view={view}
                onEventClick={onEventClick}
                onPageChange={onSelectedPage}
            />
        </div>
        <p style={{textAlign: "center"}}>
            Duty Crew- Mr. Griffin Doyle<br/>
            AOD- Mr. Roller <a href="tel:+18608823168" className="phone-link">+1 (860) 882-3168</a>


        </p>
        <Popup
            headerText={openEvent.title}
            display="bottom"
            contentPadding={false}
            showArrow={false}
            isOpen={isPopupOpen}
            buttons={["cancel"]}
            onClose={()=>{
                setPopupOpen(false)
            }}
        >
            <table className="apple-table">
                <tbody>
                <tr>
                    <td className="apple-table-header">Description</td>
                    <td>{openEvent.description}</td>
                </tr>
                <tr>
                    <td className="apple-table-header">Location</td>
                    <td>{openEvent.location}</td>
                </tr>
                <tr>
                    <td style={{width: "100%", textAlign: "center", verticalAlign: "middle"
                    }}>

                        <button className="apple-button">Add this event to my calendar</button>
                    </td>
                </tr>
                <tr>
                    <td style={{width: "100%", textAlign: "center", verticalAlign: "middle"
                    }}>

                        <button className="apple-button">Don't show me this event</button>
                    </td>
                    <td></td>
                </tr>
                </tbody>
            </table>
        </Popup>

        {/*<Toast*/}
        {/*    message={toastText}*/}
        {/*    isOpen={isToastOpen}*/}
        {/*    onClose={closeToast}*/}
        {/*/>*/}
    </div>
}
export default Teacher