import {
    Eventcalendar,
    snackbar,
    setOptions,
    Popup,
    Button,
    Input,
    Textarea,
    Switch,
    Datepicker,
    SegmentedGroup,
    SegmentedItem,
    Draggable
} from '@mobiscroll/react';
import React, {useState} from 'react';
import './drag.css';
import '@mobiscroll/react/dist/css/mobiscroll.min.css';
import {FiUpload} from 'react-icons/fi';
import AuthorizationPopup from "./AuthorizationPopup";
import Cookies from 'js-cookie';

setOptions({
    theme: 'ios',
    themeVariant: 'light'
});

const responsivePopup = {
    medium: {
        display: 'anchored',
        width: 400,
        fullScreen: false,
        touchUi: false
    }
};
const colorPopup = {
    medium: {
        display: 'anchored',
        touchUi: false,
        buttons: []
    }
}

const colors = ['#ffeb3c', '#ff9900', '#f44437', '#ea1e63', '#9c26b0', '#3f51b5', '#5ac8fa', '#009788', '#4baf4f', '#7e5d4e'];




const Student = ({myCurrentEvents, uploadedPictureUrl, setCurrentDate}) => {

    const [TOKEN, setToken] = useState("")
    const [authPopupOpen, setAuthPopupOpen] = useState(true); // State for the authorization popup
    React.useEffect(() => {
        // Code to read token from cookies and set it using setToken
        const storedToken = Cookies.get('token');
        if (storedToken) {
            setToken(storedToken);
            setAuthPopupOpen(false)
        }
    }, []);
    function onAuth(body){
        console.log(body)
        if (body.status ===  ("success")){
            setToken(body.token)
            Cookies.set('token', body.token, { expires: 1 });
        }
        setAuthPopupOpen(false)

    }

    const BASE_URL = "http://localhost:8082/"
    // const TOKEN = "13b0a187-8e10-47bc-9b8b-8485289a7db6"

    const createNewEvent = async (eventData) => {
        console.log(eventData)
        const url = BASE_URL + 'data/daily-schedule/';


        try {
            const response = await fetch(url, {
                method: 'POST',
                headers: {
                    'accept': 'application/json',
                    'Authorization': `Bearer ${TOKEN}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(eventData),
            });

            if (!response.ok) {
                throw new Error('Network response was not ok');
            }

            const data = await response.json();
            // Handle the response data as needed
            console.log('New event created:', data);
        } catch (error) {
            console.error('Error creating the event:', error);
        }
    };

    const updateEvent = async (eventData) => {
        console.log(eventData);
        const url = BASE_URL + 'data/daily-schedule/';

        try {
            const response = await fetch(url, {
                method: 'PUT',
                headers: {
                    'accept': 'application/json',
                    'Authorization': `Bearer ${TOKEN}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(eventData),
            });

            if (!response.ok) {
                throw new Error('Network response was not ok');
            }

            const data = await response.json();
            // Handle the response data as needed
            console.log('Event updated:', data);
        } catch (error) {
            console.error('Error updating the event:', error);
        }
    };

    const deleteEventCurl = async (eventData) => {
        const url = BASE_URL + 'data/daily-schedule/';

        try {
            const response = await fetch(url, {
                method: 'DELETE',
                headers: {
                    'accept': 'application/json',
                    'Authorization': `Bearer ${TOKEN}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(eventData),
            });

            if (!response.ok) {
                throw new Error('Network response was not ok');
            }

            const data = await response.json();
            // Handle the response data as needed
            console.log('Event deleted:', data);
        } catch (error) {
            console.error('Error deleting the event:', error);
        }
    };

    function convertTimeToSql(inputDate) {
        if (typeof inputDate == "string")
            return inputDate
        const utcTime = inputDate.toISOString();

// Adjust the UTC time to match the original time
        const adjustedTime = utcTime.slice(0, 11) + inputDate.toLocaleTimeString("en-US", {hour12: false}) + ".000Z";

        return (adjustedTime);

    }

    function generateShortGUID() {
        // Generate a standard GUID (UUIDv4)
        const guid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            const r = (Math.random() * 16) | 0;
            const v = c === 'x' ? r : (r & 0x3) | 0x8;
            return v.toString(16);
        });

        // Trim or truncate the GUID to fit varchar(50)
        const maxLength = 50;
        if (guid.length > maxLength) {
            return guid.slice(0, maxLength);
        }

        return guid;
    }

    // const [uploadedPictureUrl, setUploadedPictureUrl] = useState('');

    const [uploadedPicture, setUploadedPicture] = useState(null);
    const fileInputRef = React.useRef(null);


    const generateDragOptions = (id, title, color, start, end) => {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const [draggable, setDraggable] = useState(false);

        // eslint-disable-next-line react-hooks/rules-of-hooks
        const setElm = React.useCallback((elm) => {
            setDraggable(elm);
        }, []);

        return {
            id,
            title,
            color,
            start,
            end,
            drag: draggable,
            set: setElm,
        };
    };
    const viewSettings = React.useMemo(() => {
        return {
            schedule: {
                type: 'day',
                size: 1, // Set the scheduler size to 1 to make it smaller
                startTime: '06:00',
                endTime: '23:00',

            },
        };
    }, []);


    const [myEvents, setMyEvents] = React.useState(); //TODO: add request to get events from server
    const [tempEvent, setTempEvent] = React.useState(null);
    const [isOpen, setOpen] = React.useState(false);
    const [isEdit, setEdit] = React.useState(false);
    const [anchor, setAnchor] = React.useState(null);
    const [start, startRef] = React.useState(null);
    const [end, endRef] = React.useState(null);
    const [popupEventTitle, setTitle] = React.useState('');
    const [popupEventDescription, setDescription] = React.useState('');
    const [popupEventLocation, setLocation] = React.useState('');
    const [popupEventDate, setDate] = React.useState([]);
    const [popupEventStatus, setStatus] = React.useState('busy');
    const [mySelectedDate, setSelectedDate] = React.useState(new Date());
    const [colorPickerOpen, setColorPickerOpen] = React.useState(false);
    const [colorAnchor, setColorAnchor] = React.useState(null);
    const [selectedColor, setSelectedColor] = React.useState('');
    const [tempColor, setTempColor] = React.useState('');
    const colorPicker = React.useRef();
    const colorButtons = React.useMemo(() => [
        'cancel',
        {
            text: 'Set',
            keyCode: 'enter',
            handler: () => {
                setSelectedColor(tempColor);
                setColorPickerOpen(false);
            },
            cssClass: 'mbsc-popup-button-primary'
        }
    ], [tempColor]);

    const [isToastOpen, setToastOpen] = React.useState(false);
    const [draggableBlank, setDraggableBlank] = React.useState();
    const setBlankElm = React.useCallback((elm) => {
        setDraggableBlank(elm);
    }, []);

    React.useEffect(() => {
        setMyEvents(myCurrentEvents)
    }, [myCurrentEvents])


    const dragOptions = [
        generateDragOptions("2", 'All School COMPASS', '#ff0000', '2023-06-29T08:45', '2023-06-29T09:10'),
        generateDragOptions("3", 'Morning Meeting', '#ff0000', '2023-06-29T08:45', '2023-06-29T09:10'),
        generateDragOptions("1", 'Breakfast', 'rgba(50, 50, 191, 0.46)', '2023-06-29T07:30', '2023-06-29T08:30'),
        generateDragOptions("5", 'Block A', 'rgba(103, 183, 103, 0.75)', '2023-06-29T10:15', '2023-06-29T11:15'),
        generateDragOptions("6", 'Block B', 'rgba(103, 183, 103, 0.75)', '2023-06-29T11:20', '2023-06-29T12:20'),
        generateDragOptions("4", 'Block C', 'rgba(103, 183, 103, 0.75)', '2023-06-29T09:10', '2023-06-29T10:10'),
        generateDragOptions("12", 'Sitdown Lunch', 'rgba(255, 165, 0, 0.75)', '2023-06-29T12:00', '2023-06-29T12:45'),
        generateDragOptions("13", 'Buffet Lunch', 'rgba(255, 192, 203, 0.75)', '2023-06-29T12:00', '2023-06-29T13:00'),
        generateDragOptions("7", 'Block D', 'rgba(103, 183, 103, 0.75)', '2023-06-29T12:25', '2023-06-29T13:25'),
        generateDragOptions("8", 'Block E', 'rgba(103, 183, 103, 0.75)', '2023-06-29T13:30', '2023-06-29T14:30'),
        generateDragOptions("9", 'Block F', 'rgba(103, 183, 103, 0.75)', '2023-06-29T14:35', '2023-06-29T15:35'),
        generateDragOptions("10", 'Block G', 'rgba(103, 183, 103, 0.75)', '2023-06-29T15:40', '2023-06-29T16:40'),
        generateDragOptions("11", 'Dinner', 'rgba(128, 128, 128, 0.75)', '2023-06-29T18:00', '2023-06-29T19:00'),

    ];

    const saveEvent = React.useCallback(() => {
        const newEvent = {
            id: tempEvent.id,
            title: popupEventTitle,
            description: popupEventDescription,
            start: popupEventDate[0],
            end: popupEventDate[1],
            status: popupEventStatus,
            color: selectedColor,
            resource: tempEvent.resource,
            location: popupEventLocation,
        };
        if (isEdit) {
            // update the event in the list
            const index = myEvents.findIndex(x => x.id === tempEvent.id);
            ;
            const newEventList = [...myEvents];

            newEventList.splice(index, 1, newEvent);
            setMyEvents(newEventList);
            console.log(convertTimeToSql(newEvent.start))

            updateEvent({
                ...newEvent,
                start: convertTimeToSql(newEvent.start),
                end: convertTimeToSql(newEvent.end),
            })
            // here you can update the event in your storage as well
            // ...
        } else {

            // add the new event to the list
            newEvent.id = generateShortGUID()
            setMyEvents([...myEvents, newEvent]);
            createNewEvent({
                ...newEvent,
                start: convertTimeToSql(newEvent.start),
                end: convertTimeToSql(newEvent.end),
            })

            console.log(myEvents)
            // here you can add the event to your storage as well
            // ...
        }
        setSelectedDate(popupEventDate[0]);
        // close the popup
        setOpen(false);
    }, [isEdit, myEvents, popupEventDate, popupEventDescription, popupEventStatus, popupEventTitle, tempEvent, selectedColor, popupEventLocation]);

    const deleteEvent = React.useCallback((event) => {
        setMyEvents(myEvents.filter(item => item.id !== event.id));
        deleteEventCurl(event)
        setTimeout(() => {
            snackbar({
                button: {
                    action: () => {
                        setMyEvents(prevEvents => [...prevEvents, event]);
                    },
                    text: 'Undo'
                },
                message: 'Event deleted'
            });
        });
    }, [myEvents]);

    const loadPopupForm = React.useCallback((event) => {
        setTitle(event.title);
        setDescription(event.description);
        setLocation(event.location);
        setDate([event.start, event.end]);
        setStatus(event.status || 'busy');
    }, []);

    // handle popup form changes

    const titleChange = React.useCallback((ev) => {
        setTitle(ev.target.value);
    }, []);

    const descriptionChange = React.useCallback((ev) => {
        setDescription(ev.target.value);
    }, []);
    const locationChange = React.useCallback((ev) => {
        setLocation(ev.target.value);
    }, []);


    const dateChange = React.useCallback((args) => {
        setDate(args.value);
    }, []);

    const statusChange = React.useCallback((ev) => {
        setStatus(ev.target.value);
    }, []);

    const onDeleteClick = React.useCallback(() => {
        deleteEvent(tempEvent);
        setOpen(false);
    }, [deleteEvent, tempEvent]);

    // scheduler options

    const onSelectedDateChange = React.useCallback((event) => {
        setSelectedDate(event.date);
    }, []);
    const onSelectedPage = React.useCallback((event) => {
        setCurrentDate(event.firstDay);
    }, []);

    const onEventClick = React.useCallback((args) => {
        setEdit(true);
        setTempEvent({...args.event});
        setSelectedColor(args.event.color);
        // fill popup form with event data
        loadPopupForm(args.event);
        setAnchor(args.domEvent.target);
        setOpen(true);
    }, [loadPopupForm]);

    const onEventCreated = React.useCallback((args) => {
        setEdit(false);
        setTempEvent(args.event)
        let el = (dragOptions.find(r => r.id === args.event.id))
        setSelectedColor(el ? el.color : "rgba(220,114,114,0.6)");
        // fill popup form with event data
        loadPopupForm(args.event);
        setAnchor(args.target);
        // open the popup
        setOpen(true);
    }, [loadPopupForm]);

    const onEventDeleted = React.useCallback((args) => {
        deleteEvent(args.event)
    }, [deleteEvent]);

    const onEventUpdated = React.useCallback((args) => {
        console.log(args)
        updateEvent({
            ...args.event,
            start: convertTimeToSql(args.event.start),
            end: convertTimeToSql(args.event.end),
        })
        // here you can update the event in your storage as well, after drag & drop or resize
        // ...
    }, []);

    // datepicker options
    const controls = React.useMemo(() => ['datetime'], []);
    const responsiveOptions = React.useMemo(() => ({
        medium: {
            controls: ['calendar', 'time'],
            touchUi: false
        }
    }), []);

    // popup options
    const headerText = React.useMemo(() => isEdit ? 'Edit event' : 'New Event', [isEdit]);
    const popupButtons = React.useMemo(() => {
        if (isEdit) {
            return ['cancel', {
                handler: () => {
                    saveEvent();
                },
                keyCode: 'enter',
                text: 'Save',
                cssClass: 'mbsc-popup-button-primary'
            }];
        } else {
            return ['cancel', {
                handler: () => {
                    saveEvent();
                },
                keyCode: 'enter',
                text: 'Add',
                cssClass: 'mbsc-popup-button-primary'
            }];
        }
    }, [isEdit, saveEvent]);

    const onClose = React.useCallback(() => {
        if (!isEdit) {
            // refresh the list, if add popup was canceled, to remove the temporary event
            setMyEvents([...myEvents]);
        }
        setOpen(false);
    }, [isEdit, myEvents]);

    const selectColor = React.useCallback((color) => {
        setTempColor(color)
    }, []);

    const openColorPicker = React.useCallback((ev) => {
        selectColor(selectedColor);
        setColorAnchor(ev.currentTarget);
        setColorPickerOpen(true);
    }, [selectColor, selectedColor]);

    const changeColor = React.useCallback((ev) => {
        const color = ev.currentTarget.getAttribute('data-value');
        selectColor(color);
        if (!colorPicker.current.s.buttons.length) {
            setSelectedColor(color);
            setColorPickerOpen(false);
        }
    }, [selectColor, setSelectedColor]);

    const onPickerClose = React.useCallback(() => {
        setColorPickerOpen(false);
    }, []);

    function formatDate(originalDate) {
        const year = originalDate.getFullYear();
        const month = String(originalDate.getMonth() + 1).padStart(2, '0');
        const day = String(originalDate.getDate()).padStart(2, '0');
        const formattedDate = `${year}-${month}-${day}`;
        return formattedDate
    }


    const handleFileChange = async (event) => {
        const file = event.target.files[0];
        setUploadedPicture(file);

        const formData = new FormData();
        formData.append('image', file); // Use the 'file' variable directly instead of 'uploadedPicture'
        formData.append('date', formatDate(mySelectedDate)); // Replace with the desired date

        try {
            const response = await fetch(BASE_URL + 'data/daily-schedule/image', {
                method: 'POST',
                headers: {
                    'Authorization': 'Bearer ' + TOKEN,
                },
                body: formData,
            });

            if (response.ok) {
                const data = await response.json();
                // setUploadedPictureUrl(data.imageURL);
            } else {
                // Handle server errors
            }
        } catch (error) {
            // Handle fetch errors
        }
    };


    return <div>
        {authPopupOpen && (
            <AuthorizationPopup onAuthorize={onAuth} authPopupOpen={authPopupOpen} setAuthPopupOpen={setAuthPopupOpen}/>
        )}
        <div className="upload-button-container">
            <Button
                onClick={() => fileInputRef.current.click()}
                color="apple" // Replace with the desired Apple color
                className="upload-button"
            >
                <FiUpload/> Upload Daily Picture
            </Button>
            <input
                type="file"
                accept="image/*"
                ref={fileInputRef}
                onChange={handleFileChange}
                style={{display: 'none'}}
            />
        </div>
        <div className="picture-window">
            {!uploadedPicture && uploadedPictureUrl && <img src={uploadedPictureUrl} alt="Uploaded"/>}
            {uploadedPicture && (
                <img src={URL.createObjectURL(uploadedPicture)} alt="Preview"/>
            )}
        </div>

        <table className="scheduler_box">
            <tbody>
            <tr>
                <td width={"70%"}>
                    <Eventcalendar
                        height={"800px"}
                        view={viewSettings}
                        data={myEvents}
                        clickToCreate="double"
                        dragToCreate={true}
                        externalDrop={true}
                        dragToMove={true}
                        dragToResize={true}
                        selectedDate={mySelectedDate}
                        onSelectedDateChange={onSelectedDateChange}
                        onEventClick={onEventClick}
                        onEventCreated={onEventCreated}
                        onEventDeleted={onEventDeleted}
                        onEventUpdated={onEventUpdated}
                        onPageChange={onSelectedPage}
                    />
                </td>
                <td>
                    <div className="mbsc-col-sm-3">
                        <div className="mbsc-form-group-title">Available tasks</div>
                        {dragOptions.map(e => {
                            return (
                                <div ref={e.set} className="external-drop-task" style={{background: e.color}}
                                     key={e.id}>
                                    <div>{e.title}</div>
                                    <Draggable dragData={e} element={e.drag}/>
                                </div>
                            )
                        })}

                        <div ref={setBlankElm} className="external-drop-task external-drop-task-blank">
                            <div>Blank event</div>
                            <Draggable element={draggableBlank}/>
                        </div>
                    </div>
                </td>
            </tr>
            </tbody>
        </table>

        <Popup
            display="bottom"
            fullScreen={true}
            contentPadding={false}
            headerText={headerText}
            anchor={anchor}
            buttons={popupButtons}
            isOpen={isOpen}
            onClose={onClose}
            responsive={responsivePopup}
        >
            <div className="mbsc-form-group">
                <Input label="Title" value={popupEventTitle} onChange={titleChange}/>
                <Textarea label="Description" value={popupEventDescription} onChange={descriptionChange}/>
                <Textarea label="Location" value={popupEventLocation} onChange={locationChange}/>
            </div>
            <div className="mbsc-form-group">
                <Input ref={startRef} label="Starts"/>
                <Input ref={endRef} label="Ends"/>
                <Datepicker
                    select="range"
                    controls={controls}
                    touchUi={true}
                    startInput={start}
                    endInput={end}
                    showRangeLabels={false}
                    responsive={responsiveOptions}
                    onChange={dateChange}
                    value={popupEventDate}
                />
                <div onClick={openColorPicker} className="event-color-c">
                    <div className="event-color-label">Color</div>
                    <div className="event-color" style={{background: selectedColor}}></div>
                </div>
                <SegmentedGroup onChange={statusChange}>
                    <SegmentedItem value="busy" checked={popupEventStatus === 'busy'}>Show as mandatory</SegmentedItem>
                    <SegmentedItem value="free" checked={popupEventStatus === 'free'}>Show as free</SegmentedItem>
                </SegmentedGroup>
                {isEdit && <div className="mbsc-button-group">
                    <Button className="mbsc-button-block" color="danger" variant="outline" onClick={onDeleteClick}>Delete
                        event</Button>
                </div>}
            </div>
        </Popup>
        <Popup
            display="bottom"
            contentPadding={false}
            showArrow={false}
            showOverlay={false}
            anchor={colorAnchor}
            isOpen={colorPickerOpen}
            buttons={colorButtons}
            responsive={colorPopup}
            ref={colorPicker}
            onClose={onPickerClose}
        >
            <div className="crud-color-row">
                {colors.map((color, index) => {
                    if (index < 5) {
                        return <div key={index} onClick={changeColor}
                                    className={"crud-color-c " + (tempColor === color ? 'selected' : '')}
                                    data-value={color}>
                            <div className="crud-color mbsc-icon mbsc-font-icon mbsc-icon-material-check"
                                 style={{background: color}}></div>
                        </div>
                    } else return null;
                })}
            </div>
            <div className="crud-color-row">
                {colors.map((color, index) => {
                    if (index >= 5) {
                        return <div key={index} onClick={changeColor}
                                    className={"crud-color-c " + (tempColor === color ? 'selected' : '')}
                                    data-value={color}>
                            <div className="crud-color mbsc-icon mbsc-font-icon mbsc-icon-material-check"
                                 style={{background: color}}></div>
                        </div>
                    } else return null;
                })}
            </div>
        </Popup>
    </div>
}

export default Student;