import logo from './logo.svg';
import './App.css';
import React, {useState} from "react";
import Teacher from "./Teacher";
import Student from "./Student";
import {
    BrowserRouter,
    Routes,
    Route,
    Link,
} from "react-router-dom";

const BASE_URL = "http://localhost:8082/"

function App() {

    const [currentEvents, setCurrentEvents] = React.useState([])
    const [currentDate, setCurrentDate]= React.useState(new Date)
    const [uploadedPictureUrl, setUploadedPictureUrl] = useState('');

    const [uploadedPicture, setUploadedPicture] = useState(null);

    function formatDate(originalDate) {
        const year = originalDate.getFullYear();
        const month = String(originalDate.getMonth() + 1).padStart(2, '0');
        const day = String(originalDate.getDate()).padStart(2, '0');
        const formattedDate = `${year}-${month}-${day}`;
        return formattedDate
    }


    React.useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(
                    `${BASE_URL}data/daily-schedule/events?date=${formatDate(currentDate)}`,
                    {
                        method: 'GET',
                        headers: {
                            accept: 'application/json',
                        },
                    }
                );
                const data = await response.json();
                setCurrentEvents(data.events?data.events:[]); // Assuming the response is an array of events
                // setLoading(false);
            } catch (error) {
                console.error('Error fetching events:', error);
                // setLoading(false);
            }
        };

        fetchData();
    }, [currentDate]);

    React.useEffect(() => {
        const fetchPictureForToday = async () => {
            try {
                const response = await fetch(BASE_URL+`data/daily-schedule/image?date=${formatDate(currentDate)}`, {
                    headers: {
                        'accept': 'image/*',
                    },
                });

                if (response.ok) {
                    const blob = await response.blob();
                    setUploadedPictureUrl(URL.createObjectURL(blob));
                } else {
                    // Handle server errors
                }
            } catch (error) {
                // Handle fetch errors
            }
        };
        fetchPictureForToday();
    }, [currentDate]);


  return (
      <BrowserRouter>
          <Routes>
              <Route exact path="/" element={<Teacher myCurrentEvents={currentEvents} uploadedPictureUrl={uploadedPictureUrl} setCurrentDate={setCurrentDate}/>}/>
              <Route path="/student" element={ <Student myCurrentEvents={currentEvents} uploadedPictureUrl={uploadedPictureUrl} setCurrentDate={setCurrentDate}/>}/>
          </Routes>
      </BrowserRouter>
  );
}

export default App;
