import React, {useState} from 'react';
import {Popup} from "@mobiscroll/react";
import './auth.css';

const AuthorizationPopup = ({ onAuthorize, authPopupOpen, setAuthPopupOpen }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const handleAuthorize = async () => {
        try {
            const response = await fetch('http://127.0.0.1:8082/auth/login', {
                method: 'POST',
                headers: {
                    'accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    username: username,
                    password: password,
                }),
            });

            if (response.ok) {
                const data = await response.json();
                // Assuming the response contains a token
                // const token = data.token;

                // Set the token in local storage or context/state for future requests
                // Example: localStorage.setItem('accessToken', token);

                // Close the authorization popup and trigger onAuthorize callback
                onAuthorize(data);
            } else {
                // Handle authorization error here
                console.error('Authorization failed');
            }
        } catch (error) {
            console.error('Authorization error:', error);
        }
    };

    return (
        <Popup
            display="center"
            contentPadding={false}
            showArrow={false}
            showOverlay={true}
            isOpen={authPopupOpen}
            onClose={() => setAuthPopupOpen(false)}
        >
            <div className="auth-popup">
                <h2>Authorization Required</h2>
                <div>
                    <input
                        type="text"
                        placeholder="Username"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                    />
                </div>
                <div>
                    <input
                        type="password"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                </div>
                <div>
                    <button onClick={handleAuthorize}>Authorize</button>
                </div>
            </div>
        </Popup>
    );
};

export default AuthorizationPopup